
import { Component } from 'react';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { withAuthRedirect } from '../../hoc/withAuthRedirect';
import { newMessageDialogs, getDialogs } from '../../Redux/dialogReducer'
import {getStatus} from '../../Redux/profileReducer'
import Messages from './Messages';

class MessagesContainer extends Component {
    
    componentDidMount() {
        debugger
        //this.props.getStatus(13195)
        this.props.getDialogs()
    }
    sendMessageContainer = (formObj) => {
            this.props.newMessageDialogs(formObj.newMessageBody)
            //dispatch(newMessageActionCreator(formObj.newMessageBody))
            formObj.newMessageBody=""
        }
    render(){
        return (
            <Messages dialogs={this.props.dialogs} message={this.props.message} sendMessageContainer={this.props.sendMessageContainer} />
        )
    }
}

const mapStateToProps = (state /*, ownProps*/) => {
    
    return {
        dialogs: state.PostsPage.dialogs,
        message: state.PostsPage.messages,
        
    }
}
const mapDispatchToProps =(dispatch) => { 
    
    return{
        /* sendMessageContainer: (formObj) => {
            dispatch(newMessageDialogs(formObj.newMessageBody))
            //dispatch(newMessageActionCreator(formObj.newMessageBody))
            formObj.newMessageBody=""
        } */
        
        newMessageDialogs,
        getDialogs, getStatus
    }
}

export default compose(
    connect(mapStateToProps,mapDispatchToProps),
    withAuthRedirect
)(MessagesContainer);
