import classes from './AboutDataForm.module.css'
import Contact from './About'
import { Formik, Field, Form } from 'formik';
import {Redirect, NavLink,  useHistory } from 'react-router-dom'

let AboutDataForm=(props)=>{
    debugger
    const history = useHistory();
    
    return<div className={classes.MainAbout}>
        <div className={classes.heading}>Here you can edit information about yourself </div>
        <Formik
            initialValues={{
            lookingForAJob: (!props.props.profile.lookingForAJob? false :props.props.profile.lookingForAJob),
            LookingForAJobDescription: (props.props.profile.lookingForAJobDescription? props.props.profile.lookingForAJobDescription :''),
            AboutMe: (props.props.profile.aboutMe? props.props.profile.aboutMe :"")
            }}
            onSubmit={async (values) => {
                
                props.setAboutData(values,props.props.profile.fullName,props.props.profile.userId)
                
                history.push("/profile")
                //props.history.location.pathname.push("/profile");
                //props.history.location.pathname="/profile"
            }}
            >
            {({ values }) => (
            <Form><div className={classes.About}>
                <label className={classes.info}>Looking for a job</label>
                    <div><Field type="checkbox" name="lookingForAJob" />
                    {`${values.lookingForAJob? "yes": "no"}`}</div>
                
                <label className={classes.info} htmlFor="LookingForAJobDescription">My hard skills:</label> 
                <Field id="LookingForAJobDescription" component="textarea" name="LookingForAJobDescription" placeholder="HTML SCC JS React Redux" />
                
                <label className={classes.info} htmlFor="AboutMe">About me: </label>
                <Field id="AboutMe" component="textarea" name="AboutMe" placeholder="describe yourself" />
                <div className={classes.advertisement} ><img className={classes.advertisement} src={'https://sun9-50.userapi.com/impf/c836137/v836137892/50859/gSlB71Q9BVE.jpg?size=1280x960&quality=96&proxy=1&sign=a16c1b01aadf358376f83137b2c8ec1e&type=album'}/>
                    place for advertisement</div>
                <div>
                    {Object.keys(props.props.profile.contacts).map(key=>{
                        return<div key={key}>
                            <label className={classes.About} htmlFor="Contacts">
                                {key}:
                                <Field id={key} name={`contacts.${key}`}  value={props.props.profile.contacts[key]} placeholder={key} />
                            </label>
                        </div>
                    })}
                </div> 

                <div></div>
                <div><button type="submit">Submit</button><NavLink to={'./profile/'}><button >cancel</button></NavLink></div>
            </div></Form>
        )}
        </Formik>
        
                
                {/* <div>
                    {Object.keys(props.props.profile.contacts).map(key=>{
                        return<Contact key={key} title={key} value={props.props.profile.contacts[key]}/>
                    })}
                </div> */}
    </div>
}

export default AboutDataForm