import StatusWithHooks from '../About/Status/StatusWithHooks'
import classes from './Pic.module.css'

export default function Pic (props){
    
    let onMainPhotoSet=(e)=>{
        debugger
        if(e.target.files.length){
            props.setUserPhoto(e.target.files[0])
        }
    }

    let photo=()=>{
        if(props.props.profile!=null){
            if(props.props.profile.photos!=null){
                return props.props.profile.photos.small
            }
        }
        return 
    }
    return(
        <div className={classes.Pic}>
        {props.props.profile!=null ?
        <img src={props.props.profile.photos.large!=null ? props.props.profile.photos.large :'https://sun9-33.userapi.com/impf/xZea1cGfLOj-MhChgkIKsgDSZGEuGSOVHs1WHA/YtDlw7A5LTo.jpg?size=2476x1212&quality=96&proxy=1&sign=9da40de598b9f92db3f300c6d278e04d'} alt=""></img>
        :<img src={'https://sun9-33.userapi.com/impf/xZea1cGfLOj-MhChgkIKsgDSZGEuGSOVHs1WHA/YtDlw7A5LTo.jpg?size=2476x1212&quality=96&proxy=1&sign=9da40de598b9f92db3f300c6d278e04d'} alt=""></img>
        }
            {props.isOwner &&  
                <span>
                    <input className={classes.hidden} name="file" id="file" type={'file'} /> 
                    <label for="file" onChange={onMainPhotoSet}></label>
                </span>
            } 
        <StatusWithHooks status={props.props.status} updateStatus={props.updateStatus}/>
        </div>
    )
}