import React from 'react'
import { connect } from 'react-redux';
import Content from './Content';
import classes from'./Content.module.css';
import {getUserProfile,getStatus, updateStatus, setUserPhoto, setAboutData} from '../../Redux/profileReducer'
import {  withRouter } from 'react-router-dom';
import { withAuthRedirect } from '../../hoc/withAuthRedirect';
import { compose } from 'redux';

class ContentContainer extends React.Component {
    
    refreshProfile(){
        let userId = this.props.match.params.userId
        if(!userId){
            userId=this.props.authUserId
        }
        this.props.getUserProfile(userId)
        this.props.getStatus(userId)
    }
    componentDidMount() {
        this.refreshProfile()
        /* axios.get(`https://social-network.samuraijs.com/api/1.0/profile/`+ userId) */
        /* usersAPI.getProfile(userId)
        .then(response =>{
            console.log(response.data)
            this.props.setUserProfile(response.data)
        }) */
    }
    componentDidUpdate(prevProps,prevState,snapshot){
        if(this.props.match.params.userId !==prevProps.match.params.userId)
        this.refreshProfile()
    }


    render() {
        
        
        //if(!this.props.isAuth)return <Redirect to={'/login'}/>
        return (
            <div>
                <Content {...this.props} getStatus={this.props.getStatus} 
                isOwner={!this.props.match.params.userId} updateStatus={this.props.updateStatus}
                setUserPhoto={this.props.setUserPhoto} setAboutData={this.props.setAboutData}/>
                
            </div>
        )
    }
}

let mapStateToProps = (state) => ({
    //usersInfo: state.usersPage,
    state: state.ContentPage,
    authUserId: state.auth.userId,
    isAuth: state.auth.isAuth
})

export default compose(
    connect(mapStateToProps,
        {getUserProfile, getStatus, updateStatus, setUserPhoto, setAboutData}),
    withRouter,
    withAuthRedirect
)(ContentContainer)