import { useState} from 'react'
import Loader from '../../UI/Loader/Loader'
import classes from './About.module.css'
import AboutDataForm from './AboutDataForm'
import classnames from 'classnames'
import {NavLink} from 'react-router-dom'

export default function About (props){
    
    let [editMode, setEditMode] = useState(false)
    
    if(!props.props.profile ){
        return <Loader />
    }
    
    const activateEditMode =() =>{
        setEditMode(true)
    }
    const deactivateEditMode =()=>{
        setEditMode(false)
        //props.updateAbout(props)
    }

    return(
        <div /* className={classes.About} */> 
            
            
            <div className={classes.MyName}>{props.props.profile.fullName}
            </div>
            {editMode
            ? <AboutDataForm {...props} deactivateEditMode={deactivateEditMode} setAboutData={props.setAboutData}/> 
            : <AboutData {...props} isOwner={props.isOwner}  activateEditMode={activateEditMode}/>}
            
        </div>
        
    )
}


let AboutData=(props)=>{
    //debugger
    return<div className={classes.About}>
        
        <div className={classes.info}>Looking for a job: </div>
            <div className={classes.answer}>{props.props.profile.lookingForAJob? 'yes':'no'}</div>
        <div className={classes.info}>My hard skills: </div>
            <div className={classes.answer}>{props.props.profile.lookingForAJobDescription}</div>
        <div className={classes.info}>About me:</div>
        <div className={classes.answer}>{props.props.profile.aboutMe}</div>
                
                
            {props.isOwner &&<div><NavLink to={'/settings'}><button className={classes.EditBTN}>Edit</button></NavLink></div>}
                <div className={classes.contacts}>
                    {Object.keys(props.props.profile.contacts).map(key=>{
                        return <Contact  key={key} title={key} name={'classes.'+key} value={props.props.profile.contacts[key]}/>
                    })}
                </div>
        
    </div>
}

export let Contact =({title,value, name})=>{
    
    return (
        true? <ul className={classes.fss}>
            <a href={value} >
            <li className={classnames({[classes.vk]: title==='vk'},{[classes.facebook]: title==='facebook'},
                {[classes.website]: title==='website'},{[classes.twitter]: title==='twitter'},
                {[classes.instagram]: title==='instagram'},{[classes.youtube]: title==='youtube'},
                {[classes.github]: title==='github'},{[classes.mainLink]: title==='mainLink'},
                classes.socialMedia)}></li>
            </a>
        </ul> 
        : null
        
    )

    //return <div className={classnames(classes.socialMedia, name, { [classes.foo]: !value })}>{title}: {value}</div>
}
