import { authAPI, loginAPI, securityAPI } from "../api/api";


let initialState = {
    userId: null,
    email: null,
    login:null,
    isAuth: false,
    captchaUrl: null
}


export  const authReducer = (state=initialState, action) => {
    
    switch(action.type){
        case 'SETUSERDATA':{
            return {
                ...state,
                ...action.data
            }
        }
        case 'GETCAPTCHA':{
            return {
                ...state,
                ...action.payload
            }
        }
    
    default:
        return state
    }
}

    
    export function setUserData(userId, email, login, isAuth) {
        return {type: 'SETUSERDATA', data:{ userId, email, login, isAuth} }
    };
    export function getCaptchaUrlSuccess(captchaUrl) {
        return {type: 'GETCAPTCHA', payload:{ captchaUrl} }
    };

    export const getAuthUserData = () => (dispatch) =>{
        return authAPI.me()
        .then(response =>{
            if(response.data.resultCode===0){
                let {id, email, login} =response.data.data
                dispatch(setUserData(id, email, login, true))
            }
        })
    }
    function errorResponse(messages){
        
        alert(messages)
    }
    export const login = (email, password, rememberMe, captcha) => async(dispatch) =>{
        let response= await loginAPI.login(email, password, rememberMe, captcha)
        debugger
            if(response.data.resultCode===0){
                dispatch(getAuthUserData())
            }
            
            if(response.data.resultCode!==0){
                if(response.data.resultCode===10){
                dispatch(getCaptchaUrl())
            }
                errorResponse(response.data.messages)
            }
    }
    export const getCaptchaUrl = () => async(dispatch) =>{
        debugger
        const response= await securityAPI.getCaptchaUrl()
        const captchaUrl= response.data.url;

        dispatch(getCaptchaUrlSuccess(captchaUrl))
            
    }
    export const logout = () => async(dispatch) =>{
        let response = await loginAPI.logout()
        
            if(response.data.resultCode===0){
                dispatch(setUserData(null, null, null, false))
            }
        
    }


    