import React from 'react'
import { connect } from 'react-redux';
import AboutDataForm from '../Content/About/AboutDataForm';
import {setAboutData} from '../../Redux/profileReducer'
import {Redirect} from 'react-router-dom'

import classes from'./Settings.module.css';


function Settings (props) {
    
    if(props.state.profile===null)return (<Redirect to={'/profile'}/>)
        
    return (
        <div className={classes.Settings}>
            
            
            <AboutDataForm props={props.state} isOwner={props.isOwner} setAboutData={props.setAboutData}/>
            
        </div>
    )
    
}
const mapStateToProps=(state) =>({
    state: state.ContentPage
})

export default connect(mapStateToProps, {setAboutData})(Settings);
